/* ------------------------------------- global settings */
* {
	margin: 0;
	padding: 0;
}

body {
	font-family: "Changa", sans-serif;
	background-color: #b4eedd;
}

.sound {
	color:#41b39a;
}

html,
p,
.instructions,
.sound,
.controls,
.contact,
.hidden {
    display: flex;
    justify-content: center;
    align-items: center;
}

.instructions {
	padding:0;
}

a {
	color:#41b39a;
	font-size:xx-large;
}

a:hover {
	color:#54d1b6;
}

p {
	font-size: small;
}

.hidden {
	color: #b4eedd;
}
